.gallery__card-list {
  width: 100%;
  list-style-type: none;
  margin: 50px 0 30px;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, 360px);
  gap: 20px;
  justify-content: center;
}

@media screen and (max-width: 524px) {
  .gallery__card-list {
    grid-template-columns: repeat(auto-fit, 300px);
  }
}